'use client'

import { useState, useEffect } from 'react'
import { useAuth } from '@/lib/auth/AuthContext'
import { LoadingSpinner } from '@/components/ui/LoadingSpinner'
import { apiClient } from '@/lib/api/client'
import { showErrorToast } from '@/lib/utils/errorHandling'
import Calendar, { CalendarEvent } from '@/components/ui/Calendar'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import {
  Calendar as CalendarIcon,
  Clock,
  User,
  Phone,
  Video,
  MapPin,
  Plus,
} from 'lucide-react'

interface Appointment {
  id: string
  leadName: string
  time: string
  date: string
  status: 'confirmed' | 'pending' | 'cancelled'
  type: 'phone' | 'video' | 'in-person'
  description?: string
  location?: string
  leadPhone?: string
}

// Mock data for appointments
const mockAppointments: Appointment[] = [
  {
    id: '1',
    leadName: 'John Smith',
    time: '10:00 AM',
    date: '2024-01-15',
    status: 'confirmed',
    type: 'video',
    description: 'Property viewing consultation',
    location: 'Zoom Meeting',
  },
  {
    id: '2',
    leadName: 'Sarah Johnson',
    time: '2:30 PM',
    date: '2024-01-15',
    status: 'pending',
    type: 'phone',
    description: 'Initial property discussion',
    leadPhone: '+65 9123 4567',
  },
  {
    id: '3',
    leadName: 'Mike Chen',
    time: '11:00 AM',
    date: '2024-01-16',
    status: 'confirmed',
    type: 'in-person',
    description: 'Property site visit',
    location: '123 Marina Bay, Singapore',
  },
]

export default function AgentCalendar() {
  const { user } = useAuth()
  const [appointments, setAppointments] = useState<Appointment[]>(mockAppointments)
  const [loading, setLoading] = useState(true)
  const [selectedDate, setSelectedDate] = useState<Date>(new Date())
  const [selectedEvent, setSelectedEvent] = useState<CalendarEvent | null>(null)

  // Transform appointments to calendar events
  const calendarEvents: CalendarEvent[] = appointments.map(appointment => ({
    id: appointment.id,
    title: `${appointment.leadName} - ${appointment.type} meeting`,
    date: new Date(appointment.date),
    startTime: appointment.time,
    description: appointment.description,
    location: appointment.location,
    attendees: [appointment.leadName],
    color: appointment.status === 'confirmed' ? 'green' : 
           appointment.status === 'pending' ? 'yellow' : 'red',
    type: 'appointment',
  }))

  // Fetch appointments data
  useEffect(() => {
    const fetchAppointments = async () => {
      if (!user?.id) {
        setLoading(false)
        return
      }

      try {
        setLoading(true)
        // TODO: Replace with actual API call
        // const response = await apiClient.get(`/api/appointments/agent/${user.id}`)
        // setAppointments(response.data)
        
        // For now, use mock data
        await new Promise(resolve => setTimeout(resolve, 500)) // Simulate API call
        setAppointments(mockAppointments)
      } catch (error) {
        console.error('Failed to fetch appointments:', error)
        showErrorToast(error, 'Failed to load appointments')
      } finally {
        setLoading(false)
      }
    }

    fetchAppointments()
  }, [user?.id])

  const handleDateSelect = (date: Date) => {
    setSelectedDate(date)
  }

  const handleEventClick = (event: CalendarEvent) => {
    setSelectedEvent(event)
  }

  const getAppointmentsForDate = (date: Date) => {
    return appointments.filter(appointment => {
      const appointmentDate = new Date(appointment.date)
      return appointmentDate.toDateString() === date.toDateString()
    })
  }

  const selectedDateAppointments = getAppointmentsForDate(selectedDate)

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'confirmed': return 'bg-green-100 text-green-800'
      case 'pending': return 'bg-yellow-100 text-yellow-800'
      case 'cancelled': return 'bg-red-100 text-red-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'video': return <Video className="h-4 w-4" />
      case 'phone': return <Phone className="h-4 w-4" />
      case 'in-person': return <MapPin className="h-4 w-4" />
      default: return <CalendarIcon className="h-4 w-4" />
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold flex items-center gap-2">
            <CalendarIcon className="h-6 w-6" />
            Calendar
          </h1>
          <p className="text-muted-foreground">
            Manage your appointments and schedule
          </p>
        </div>
        <Button>
          <Plus className="h-4 w-4 mr-2" />
          New Appointment
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Calendar */}
        <div className="lg:col-span-2">
          <Calendar
            events={calendarEvents}
            selectedDate={selectedDate}
            onDateSelect={handleDateSelect}
            onEventClick={handleEventClick}
            showEvents={true}
          />
        </div>

        {/* Appointment Details */}
        <div className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">
                {selectedDate.toLocaleDateString('en-US', { 
                  weekday: 'long', 
                  year: 'numeric', 
                  month: 'long', 
                  day: 'numeric' 
                })}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {selectedDateAppointments.length > 0 ? (
                <div className="space-y-4">
                  {selectedDateAppointments.map((appointment) => (
                    <div key={appointment.id} className="border rounded-lg p-4">
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center gap-2">
                          {getTypeIcon(appointment.type)}
                          <span className="font-medium">{appointment.leadName}</span>
                        </div>
                        <Badge className={getStatusColor(appointment.status)}>
                          {appointment.status}
                        </Badge>
                      </div>
                      <div className="flex items-center text-sm text-muted-foreground mb-2">
                        <Clock className="h-4 w-4 mr-1" />
                        {appointment.time}
                      </div>
                      {appointment.description && (
                        <p className="text-sm text-muted-foreground mb-2">
                          {appointment.description}
                        </p>
                      )}
                      {appointment.location && (
                        <div className="flex items-center text-sm text-muted-foreground">
                          <MapPin className="h-4 w-4 mr-1" />
                          {appointment.location}
                        </div>
                      )}
                      {appointment.leadPhone && (
                        <div className="flex items-center text-sm text-muted-foreground">
                          <Phone className="h-4 w-4 mr-1" />
                          {appointment.leadPhone}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center text-muted-foreground py-8">
                  <CalendarIcon className="h-8 w-8 mx-auto mb-2 opacity-50" />
                  <p>No appointments scheduled for this date</p>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
